// src/api/order-log.ts
import request from '../utils/request'

// 搜索订单日志
export function searchOrderLogs(params: {
  merchant_order_no?: string
  platform_order_no?: string
  trace_id?: string
}) {
  return request({
    url: '/api/v1/admin/order-log/search',
    method: 'get',
    params
  })
}

// 获取订单日志详情
export function getOrderLogDetail(id: string) {
  return request({
    url: `/api/v1/admin/order-log/detail/${id}`,
    method: 'get'
  })
}




