<template>
  <div class="order-log-container">
    <!-- 页面标题 -->
    <div class="page-header">
      <h2>
        <el-icon><Document /></el-icon>
        订单日志查询
      </h2>
      <p class="page-description">支持按商户订单号、平台订单号、TraceId查询订单日志</p>
    </div>

    <!-- 搜索表单 -->
    <el-card class="search-card" shadow="never">
      <el-form
        ref="searchFormRef"
        :model="searchForm"
        :rules="searchRules"
        label-width="120px"
        @submit.prevent="handleSearch"
      >
        <el-row :gutter="20">
          <el-col :span="8">
            <el-form-item label="商户订单号" prop="merchant_order_no">
              <el-input
                v-model="searchForm.merchant_order_no"
                placeholder="请输入商户订单号"
                clearable
                @keyup.enter="handleSearch"
              />
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="平台订单号" prop="platform_order_no">
              <el-input
                v-model="searchForm.platform_order_no"
                placeholder="请输入平台订单号"
                clearable
                @keyup.enter="handleSearch"
              />
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="TraceId" prop="trace_id">
              <el-input
                v-model="searchForm.trace_id"
                placeholder="请输入TraceId"
                clearable
                @keyup.enter="handleSearch"
              />
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-form-item>
              <el-button type="primary" @click="handleSearch" :loading="loading">
                <el-icon><Search /></el-icon>
                搜索
              </el-button>
              <el-button @click="handleReset">
                <el-icon><Refresh /></el-icon>
                重置
              </el-button>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
    </el-card>

    <!-- 订单信息 -->
    <el-card v-if="orderInfo" class="order-info-card" shadow="never">
      <template #header>
        <div class="card-header">
          <el-icon><InfoFilled /></el-icon>
          <span>订单信息</span>
        </div>
      </template>
      <el-descriptions :column="4" border>
        <el-descriptions-item label="商户订单号">
          {{ orderInfo.merchant_order_no || '-' }}
        </el-descriptions-item>
        <el-descriptions-item label="平台订单号">
          {{ orderInfo.platform_order_no || '-' }}
        </el-descriptions-item>
        <el-descriptions-item label="订单金额">
          <span class="amount">¥{{ orderInfo.order_amount || '0.00' }}</span>
        </el-descriptions-item>
        <el-descriptions-item label="支付状态">
          <el-tag :type="getPayStatusType(orderInfo.pay_status)">
            {{ getPayStatusText(orderInfo.pay_status) }}
          </el-tag>
        </el-descriptions-item>
        <el-descriptions-item label="创建时间">
          {{ orderInfo.created_at || '-' }}
        </el-descriptions-item>
        <el-descriptions-item label="支付时间">
          {{ orderInfo.pay_time || '-' }}
        </el-descriptions-item>
        <el-descriptions-item label="商户名称">
          {{ orderInfo.merchant_name || '-' }}
        </el-descriptions-item>
        <el-descriptions-item label="产品名称">
          {{ orderInfo.product_name || '-' }}
        </el-descriptions-item>
      </el-descriptions>
    </el-card>

    <!-- 日志列表 -->
    <el-card v-if="logs.length > 0" class="logs-card" shadow="never">
      <template #header>
        <div class="card-header">
          <el-icon><List /></el-icon>
          <span>日志记录 ({{ logs.length }} 条)</span>
        </div>
      </template>
      
      <div class="timeline-container">
        <el-timeline>
          <el-timeline-item
            v-for="(log, index) in logs"
            :key="log.id"
            :timestamp="log.created_at"
            :type="getLogType(log.log_level)"
            :icon="getLogIcon(log.log_level)"
            placement="top"
          >
            <el-card class="log-item">
              <div class="log-header">
                <div class="log-meta">
                  <el-tag :type="getLogLevelType(log.log_level)" size="small">
                    {{ log.log_level }}
                  </el-tag>
                  <el-tag type="info" size="small">
                    {{ log.log_type }}
                  </el-tag>
                  <el-tag type="warning" size="small">
                    {{ getNodeDescription(log.node) }}
                  </el-tag>
                </div>
                <div class="log-actions">
                  <el-button
                    type="primary"
                    size="small"
                    text
                    @click="showLogDetail(log)"
                  >
                    <el-icon><View /></el-icon>
                    详情
                  </el-button>
                </div>
              </div>
              
              <div class="log-content">
                <div class="log-message">
                  {{ getLogMessage(log.content) }}
                </div>
                
                <div v-if="log.ip || log.user_agent" class="log-extra">
                  <div v-if="log.ip" class="extra-item">
                    <el-icon><Location /></el-icon>
                    <span>{{ log.ip }}</span>
                  </div>
                  <div v-if="log.user_agent" class="extra-item">
                    <el-icon><Monitor /></el-icon>
                    <span>{{ log.user_agent }}</span>
                  </div>
                </div>
              </div>
            </el-card>
          </el-timeline-item>
        </el-timeline>
      </div>
    </el-card>

    <!-- 无结果提示 -->
    <el-empty
      v-if="!loading && logs.length === 0 && hasSearched"
      description="未找到相关日志记录"
      :image-size="120"
    >
      <el-button type="primary" @click="handleReset">重新搜索</el-button>
    </el-empty>

    <!-- 日志详情对话框 -->
    <el-dialog
      v-model="detailDialogVisible"
      title="日志详情"
      width="80%"
      :before-close="handleCloseDetail"
    >
      <div v-if="currentLog" class="log-detail">
        <el-descriptions :column="2" border>
          <el-descriptions-item label="日志ID">{{ currentLog.id }}</el-descriptions-item>
          <el-descriptions-item label="TraceId">{{ currentLog.trace_id }}</el-descriptions-item>
          <el-descriptions-item label="日志级别">
            <el-tag :type="getLogLevelType(currentLog.log_level)">
              {{ currentLog.log_level }}
            </el-tag>
          </el-descriptions-item>
          <el-descriptions-item label="日志类型">{{ currentLog.log_type }}</el-descriptions-item>
          <el-descriptions-item label="节点">{{ getNodeDescription(currentLog.node) }}</el-descriptions-item>
          <el-descriptions-item label="创建时间">{{ currentLog.created_at }}</el-descriptions-item>
          <el-descriptions-item label="IP地址">{{ currentLog.ip || '-' }}</el-descriptions-item>
          <el-descriptions-item label="用户代理">{{ currentLog.user_agent || '-' }}</el-descriptions-item>
        </el-descriptions>
        
        <div class="log-content-detail">
          <h4>日志内容</h4>
          <template v-if="prettyJson">
            <pre class="json-pre"><code>{{ prettyJson }}</code></pre>
          </template>
          <el-empty v-else description="日志内容为空" />
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script setup lang="ts">
import { ref, reactive, computed } from 'vue'
import { ElMessage, ElMessageBox } from 'element-plus'
import { 
  Document, 
  Search, 
  Refresh, 
  InfoFilled, 
  List, 
  View, 
  Location, 
  Monitor 
} from '@element-plus/icons-vue'
import { searchOrderLogs } from '@/api/order-log'

// 搜索表单
const searchForm = reactive({
  merchant_order_no: '',
  platform_order_no: '',
  trace_id: ''
})

// 表单验证规则
const searchRules = {
  merchant_order_no: [
    { 
      validator: (rule: any, value: string, callback: Function) => {
        if (!value && !searchForm.platform_order_no && !searchForm.trace_id) {
          callback(new Error('请至少输入一个搜索条件'))
        } else {
          callback()
        }
      }, 
      trigger: 'blur' 
    }
  ]
}

// 响应式数据
const loading = ref(false)
const logs = ref<any[]>([])
const orderInfo = ref<any>(null)
const hasSearched = ref(false)
const detailDialogVisible = ref(false)
const currentLog = ref<any>(null)

// 计算属性
// 美化为字符串JSON（始终输出JSON文本）
const prettyJson = computed(() => {
  const raw = currentLog.value?.content
  if (!raw) return ''
  if (typeof raw === 'string') {
    try { return JSON.stringify(JSON.parse(raw), null, 2) } catch { return raw }
  }
  try { return JSON.stringify(raw, null, 2) } catch { return String(raw) }
})

// 搜索处理
const handleSearch = async () => {
  if (!searchForm.merchant_order_no && !searchForm.platform_order_no && !searchForm.trace_id) {
    ElMessage.warning('请至少输入一个搜索条件')
    return
  }

  loading.value = true
  hasSearched.value = true
  
  try {
    const response = await searchOrderLogs(searchForm)
    console.log('API响应:', response) // 添加调试日志
    if (response.code === 0 || response.code === 200) {
      logs.value = response.data.logs || []
      orderInfo.value = response.data.order_info || null
      console.log('日志数据:', logs.value) // 添加调试日志
      console.log('订单信息:', orderInfo.value) // 添加调试日志
      ElMessage.success('搜索成功')
    } else {
      console.error('API错误:', response) // 添加调试日志
      ElMessage.error(response.msg || response.message || '搜索失败')
    }
  } catch (error: any) {
    console.error('请求错误:', error) // 添加调试日志
    ElMessage.error('搜索失败：' + (error.message || '未知错误'))
  } finally {
    loading.value = false
  }
}

// 重置表单
const handleReset = () => {
  searchForm.merchant_order_no = ''
  searchForm.platform_order_no = ''
  searchForm.trace_id = ''
  logs.value = []
  orderInfo.value = null
  hasSearched.value = false
}

// 显示日志详情
const showLogDetail = (log: any) => {
  currentLog.value = log
  detailDialogVisible.value = true
}

// 关闭详情对话框
const handleCloseDetail = () => {
  detailDialogVisible.value = false
  currentLog.value = null
}

// 获取日志级别类型
const getLogLevelType = (level: string) => {
  const types: Record<string, string> = {
    'ERROR': 'danger',
    'WARN': 'warning',
    'INFO': 'success',
    'DEBUG': 'info'
  }
  return types[level] || 'info'
}

// 获取日志类型
const getLogType = (level: string) => {
  const types: Record<string, string> = {
    'ERROR': 'danger',
    'WARN': 'warning',
    'INFO': 'primary',
    'DEBUG': 'info'
  }
  return types[level] || 'primary'
}

// 获取日志图标
const getLogIcon = (level: string) => {
  const icons: Record<string, string> = {
    'ERROR': 'CircleClose',
    'WARN': 'Warning',
    'INFO': 'InfoFilled',
    'DEBUG': 'QuestionFilled'
  }
  return icons[level] || 'InfoFilled'
}

// 获取节点描述
const getNodeDescription = (node: string) => {
  const descriptions: Record<string, string> = {
    'PaymentPageController::oauthCallback': 'OAuth回调',
    'PaymentPageController::paymentPage': '支付页面',
    'PaymentPageController::createOrder': '创建订单',
    'PaymentPageController::callPaymentApi': '调用支付接口',
    'PaymentPageController::handlePaymentResult': '处理支付结果',
    'PaymentPageController::closeOrder': '关闭订单',
    'OrderLogService::log': '日志记录',
    'AlipayService::createOrder': '支付宝创建订单',
    'AlipayService::queryOrder': '支付宝查询订单',
    'AlipayService::closeOrder': '支付宝关闭订单',
    'WechatService::createOrder': '微信创建订单',
    'WechatService::queryOrder': '微信查询订单',
    'WechatService::closeOrder': '微信关闭订单'
  }
  return descriptions[node] || node
}

// 获取日志消息
const getLogMessage = (content: string) => {
  try {
    const data = JSON.parse(content)
    return data.message || data.msg || content
  } catch {
    return content
  }
}

// 获取支付状态类型
const getPayStatusType = (status: number) => {
  const types: Record<number, string> = {
    0: 'warning',  // 待支付
    1: 'success',  // 已支付
    2: 'info',     // 已关闭
    3: 'danger'    // 已退款
  }
  return types[status] || 'info'
}

// 获取支付状态文本
const getPayStatusText = (status: number) => {
  const texts: Record<number, string> = {
    0: '待支付',
    1: '已支付',
    2: '已关闭',
    3: '已退款'
  }
  return texts[status] || '未知'
}
</script>

